import _ from 'lodash';
import React from 'react';
import ReactDOM from 'react-dom';
import ReactDOMServer from 'react-dom/server';
import {
  Router,
  RouterContext,
  match,
  browserHistory,
  createMemoryHistory,
} from 'react-router';


import Html from 'static/Html';
import routes from 'routes';

if (typeof document !== 'undefined') {
  ReactDOM.render(
    <Router history={browserHistory} routes={routes}/>,
    document.getElementById('content')
  );
}

export default (locals, callback) => {
  const clientChunk = _.find(locals.webpackStats.compilation.chunks, { name: 'client' });
  const cssUrl = _.find(clientChunk.files, x => /\.css$/.test(x));
  const jsUrl = _.find(clientChunk.files, x => /\.js/.test(x));

  const history = createMemoryHistory();
  const location = history.createLocation(locals.path);

  match({ routes, location }, (error, redirectLocation, renderProps) => {
    const markup = ReactDOMServer.renderToString(<RouterContext {...renderProps} />);
    const html = ReactDOMServer.renderToStaticMarkup(<Html jsUrl={`${__PREFIX__}/${jsUrl}`} cssUrl={`${__PREFIX__}/${cssUrl}`} markup={markup} location={location}/>);
    callback(null, `<!doctype html>\n${html}\n`);
  });};



