import React from 'react';
import { Route } from 'react-router';

import Root from 'routes/Root';
import Home from 'routes/Home';
import XsollaPage from 'routes/XsollaPage';
import Innopolis1 from 'routes/Innopolis1';
import Innopolis2 from 'routes/Innopolis2';

export default (
  <Route component={Root}>
    <Route path={`${__PREFIX__}/`} component={Home}/>
    <Route path={`${__PREFIX__}/xsolla`} component={XsollaPage}/>
    <Route path={`${__PREFIX__}/innopolis1`} component={Innopolis1}/>
    <Route path={`${__PREFIX__}/innopolis2`} component={Innopolis2}/>
  </Route>
);



